@keyframes scale {
  0% {
    transform: scale(0.8);
    opacity: 0.7;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.animate-scale {
  animation: scale 0.3s ease forwards;
}

@keyframes shake {
  10%, 90% {
    transform: translateX(-1px);
  }
  20%, 80% {
    transform: translateX(2px);
  }
  30%, 50%, 70% {
    transform: translateX(-4px);
  }
  40%, 60% {
    transform: translateX(4px);
  }
}

.animate-shake {
  animation: shake 0.3s;
}

.verification-input {
  width: 2.5rem;
  height: 2.5rem;
  text-align: center;
  border-radius: 0.5rem;
  font-size: 1.125rem;
  font-weight: 600;
  border: 1px solid rgb(209 213 219);
}

.verification-input:focus {
  outline: none;
  border-color: #5B47FB;
  box-shadow: 0 0 0 2px rgba(91, 71, 251, 0.2);
}

.verification-input.filled {
  background-color: rgb(249 250 251);
  border-color: #5B47FB;
}

.verification-input.error {
  border-color: rgb(239 68 68);
  animation: shake 0.3s;
}

.verification-button {
  display: block;
  width: 100%;
  text-align: center;
  color: #5B47FB;
  font-weight: 500;
  padding: 0.5rem;
  border-radius: 0.375rem;
  transition: color 0.2s;
}

.verification-button:hover {
  color: #4936E8;
  background-color: rgba(91, 71, 251, 0.05);
}

.az-logo {
  font-weight: 700;
  font-size: 28px;
  font-family: Poppins, sans-serif;
  text-transform: lowercase;
  color: #5b47fb;
  letter-spacing: -1px;
  display: flex;
  align-items: center;
  position: relative;
  top: -2px;
}

/* New styles for dashboard */
.nav-link {
  display: inline-block;
  padding: 0.5rem 0;
  margin-right: 2rem;
  color: #6B7280;
  font-weight: 500;
  border-bottom: 2px solid transparent;
  transition: all 0.2s;
}

.nav-link:hover {
  color: #111827;
}

.nav-link.active {
  color: #111827;
  border-bottom-color: #111827;
}

.connection-badge {
  display: inline-flex;
  align-items: center;
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-size: 0.875rem;
  font-weight: 500;
}

.connection-badge.matched {
  background-color: #EEF2FF;
  color: #4F46E5;
}

/* Add viewport fitting styles */
body {
  min-height: 100vh;
  margin: 0;
  padding: 16px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

#test-mode-banner {
  flex-shrink: 0;
}

#widget-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  max-height: calc(100vh - 80px); /* Account for banner and padding */
  overflow: hidden;
}

/* Ensure header stays fixed */
#widget-container header {
  flex-shrink: 0;
}

/* Make main content scrollable */
#widget-container main {
  flex: 1;
  overflow-y: auto;
  min-height: 0; /* Required for Firefox */
  scrollbar-gutter: stable;
}

#widget-container main::-webkit-scrollbar {
  width: 8px;
  background: transparent;
}

#widget-container main::-webkit-scrollbar-track {
  background: transparent;
}

#widget-container main::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  visibility: hidden;
}

#widget-container main:hover::-webkit-scrollbar-thumb {
  visibility: visible;
}

/* Ensure sections fill available space */
#overview-section,
#accounts-section,
#settings-section {
  height: 100%;
}

/* Add subtle scrollbar styling */
#widget-container main::-webkit-scrollbar {
  width: 8px;
}

#widget-container main::-webkit-scrollbar-track {
  background: transparent;
}

#widget-container main::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

#widget-container main::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.2);
}

/* Adjust login view dimensions */
#step-initial,
#step-verification {
  min-height: 520px;
  max-height: 520px;
  display: flex;
  flex-direction: column;
}

#step-initial .space-y-6,
#step-verification .space-y-6 {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* Ensure consistent spacing in verification view */
#verification-inputs {
  margin: 2rem 0;
}

/* Adjust form spacing */
#initial-form {
  margin-top: auto;
}

/* Adjust widget container height */
#widget-container {
  min-height: 600px;
  max-height: 600px;
}

/* Adjust main content padding */
#widget-container main {
  padding: 1.5rem;
}

/* Adjust navigation spacing */
nav {
  margin-bottom: 1.5rem;
}

.nav-link {
  margin-right: 1.5rem;
  padding-bottom: 0.5rem;
}

/* Adjust button styles */
button.rounded-full {
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Adjust section spacing */
section {
  margin-bottom: 0;
}

/* Add marketing page styles */
.marketing-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.marketing-content {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 2rem;
  gap: 4rem;
  align-items: center;
}

@media (max-width: 1024px) {
  .marketing-content {
    grid-template-columns: 1fr;
    padding: 2rem;
    gap: 2rem;
  }
}

.hero-illustration {
  max-width: 600px;
  margin: 0 auto;
}

/* Animate the illustration */
@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

.float-animation {
  animation: float 6s ease-in-out infinite;
}

.pulse-animation {
  animation: pulse 3s ease-in-out infinite;
}

.connection-line {
  stroke-dasharray: 10;
  animation: dash 20s linear infinite;
}

@keyframes dash {
  to {
    stroke-dashoffset: 200;
  }
}

/* Add verification input styles */
.verification-input {
  width: 40px;
  height: 48px;
  text-align: center;
  font-size: 24px;
  border: 2px solid #E5E7EB;
  border-radius: 8px;
  background: white;
  color: #111827;
  transition: all 0.2s;
}

.verification-input.filled {
  border-color: #5B47FB;
  background: #EEF2FF;
}

.verification-input.error {
  border-color: #EF4444;
  background: #FEF2F2;
}

/* Add scrollbar styling */
.overflow-y-auto {
  scrollbar-width: thin;
  scrollbar-color: #E5E7EB transparent;
}

.overflow-y-auto::-webkit-scrollbar {
  width: 6px;
}

.overflow-y-auto::-webkit-scrollbar-track {
  background: transparent;
}

.overflow-y-auto::-webkit-scrollbar-thumb {
  background-color: #E5E7EB;
  border-radius: 3px;
}

.overflow-y-auto::-webkit-scrollbar-thumb:hover {
  background-color: #D1D5DB;
}
